*,
body {
  margin: 0;
  padding: 0;
  font-family: "Calibri", Times, serif;
}

html {
  font-size: 62.5%;
}

.container {
  background-image: linear-gradient(to right bottom, rgba(250, 250, 250, .7), rgba(250, 250, 250, .7)), url(img/background-small.jpg);
  background-size: cover;
  background-position: center center;
  height: 100vh;
  overflow: hidden;
  padding: 3rem;
}

#root {
  text-align: center;
  background-color: rgba(240, 230, 240, 0.8);
  padding: 2rem;
  height: calc(100vh - 10rem); /*subtracts 8rem for container and root padding top and bottom*/
}

.App {
  display: grid;
  grid-template-rows: repeat(2, min-content) 1fr;
  grid-row-gap: 3rem;
  border-radius: 3px;
  padding: 1rem 0;
}

.app__heading {
  line-height: 1.2;
  align-self: start;
  color: rgb(0, 145, 145);
  font-size: 3.5rem;
}

.btn,
.btn:link,
.btn:visited {
  justify-self: center;
  padding: 1rem 2rem;
  border-radius: 30px;
  background-color: rgb(0, 145, 145);
  border: none;
  color: white;
  font-size: 1.8rem;
  cursor: pointer;
  box-shadow: .1rem .2rem .3rem rgba(0, 0, 0, .4);
  transform: translateY(0);
  transition: all .2s ease-in-out;
}

.btn:hover {
  background-color: rgb(3, 173, 173);
}

.btn:active {
  transform: translateY(1px);
  box-shadow: .1rem .1rem .1rem rgba(31, 31, 31, 0.5);
}

.spinner {
  justify-self: center;
  align-self: center;
  width: 8rem;
}

.quote-block {
  align-self: center;

  display: grid;
  grid-row-gap: 3rem;
}

.quote__p {
  font-size: 2rem;
  color: rgb(48, 47, 47);
  line-height: 1.2;
}

.quote__author {
  font-style: italic;
  color: rgb(48, 47, 47);
  font-size: 1.8rem;
}

/******** RESPONSIVENESS *******/

@media only screen and (min-width: 300px) {
  .App {
    margin: 0 auto;
    grid-row-gap: 6rem;
  }
  .quote__p {
    font-size: 2.3rem;
    line-height: 1.1;
  }
}

@media only screen and (min-width: 500px) {
  .quote__p {
    font-size: 2.6rem;
    line-height: 1.5;
  }
  .btn {
    font-size: 2rem;
  }
}

@media only screen and (min-width: 600px) {
  #root{
    width: 80%;
    margin: 0 auto;
  }
  .quote__p {
    font-size: 2.8rem;
  }
  .App {
    margin: 0 auto;
    grid-row-gap: 8rem;
  }
  .app__heading {
    font-size: 4rem;
  }
  .btn {
    font-size: 2rem;
  }
}

@media only screen and (min-width: 700px) {
  .App {
    width: 90%;
    padding: 3rem 0;
  }
  .quote__p {
    font-size: 3rem;
  }
  .app__heading {
    font-size: 5rem;
  }
  .btn {
    font-size: 2.5rem;
  }
}

@media only screen and (min-width: 1200px) {
  .App {
    width: 80%;
  }
}